export default {
	data() {
		return {
			content: ""
		}
	},
	methods: {
		submit() {
			if(this.content.trim().length == 0){
				return uni.showToast({
					title: "消息不能为空",
					icon: "none"
				})
			}
			this.$emit('submit', this.content);
			//清空输入框内容
			this.content = "";
		}
	},
}
